<template>
    <div class="h-full ba">
        <el-row class="h-full">
            <el-col :span=24 class="h-full mt-20">
                <el-card class="w-1/2 h-auto m-auto top-1/2 Tbg">
                    <el-text class="text-white font-bold">
                        由于使用的是森空岛的Api数据,需要登录才能进行访问,请把获取的Cred令牌输入.
                    </el-text>
                    <br />
                    <el-text class="text-white font-bold">
                        Token为获取Cred一起返回的内容.
                    </el-text>
                    <el-input v-model="Cred" placeholder="Please input Cred" class="mb-3" />

                    <el-input v-model="Token" placeholder="Please input Token" />
                    <el-text v-show="check_info" class="text-red-600 font-bold">
                        当前已过期
                    </el-text>
                    <el-col>
                        <el-button style="background-color: rgba(127, 255, 212, 0.514);" type="primary"
                            @click="login">确定</el-button>
                    </el-col>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    name: 'GameInfoDynamicLogin',

    data() {
        return {
            Cred: localStorage.getItem("Cred"),
            Token: localStorage.getItem("Token")
        };
    },

    mounted() {

    },

    methods: {
        check_info() {
            return localStorage.getItem('Cred_info') == null ? false : localStorage.getItem('Cred_info')
        },
        login() {
            localStorage.setItem('Cred', this.Cred);
            localStorage.setItem('Token', this.Token);
            window.history.go(-1);
        }
    },
};
</script>

<style lang="scss" scoped>
.Tbg {
    box-shadow: 0px 0px 40px 0px black;
    border: none;
    background-color: #1b1b1b41;
    backdrop-filter: blur(3px);
}
</style>